import React, { Component, useEffect, useState } from 'react';
import moment from 'moment';
import { connect } from 'react-redux';
import globalStyles from '../../common/style';
import Medium from "../../assets/png/medium.png";
import { getPosts } from '../../modules/blogs';
import { toggleSendMessageModal } from '../../modules/static/modals';
import '@components/Blogs/index.scss';


const sWidth: number = window.innerWidth;
const Blogs: React.FunctionComponent<any> = (props) => {
    const [loaded, setLoaded] = useState<boolean>(false);
    const renderFetchBlogs: { (): void } = () => {
        if (props.posts.length === 0 && props.fetching === false) {
            props.getPosts()
        }
        setTimeout((() => {
            setLoaded(true)
        }), 1000)
    }

    useEffect(() => {
        renderFetchBlogs()
        //setLoaded(true)
    }, [])

    const renderOnePost = (item: typeof props.blogs.posts[0]) => {
        return (
            <div className="col-lg-4 justify-content-center d-flex align-items-center">
                <div className="card blog-card" style={{ margin: "20px 10px" }}>
                    <img onClick={() => window.open(item?.link, "__blank")}
                        src={item?.thumbnail || "https://mapandan.gov.ph/wp-content/uploads/2018/03/no_image.jpg"}
                        className="card-img-top"
                        width="100%"
                        height="215px"
                        style={{
                            objectFit: "cover"
                        }}
                        alt={item?.title.replace(/(&amp;)/ig, " & ")} />
                    <div className="card-body blog-body">
                        <p className="blog-information d-flex align-items-center justify-content-center">
                            <span style={{ flex: 3 }}>Yazar:
                                <a className="blog-author"
                                    style={{
                                        color: globalStyles.primaryColor,
                                        fontFamily: 'inherit',
                                        fontWeight: 'bold',
                                        marginLeft: "10px"
                                    }}
                                    target="__blank"
                                    href={props.author?.link}>
                                    {item?.author}
                                </a>
                            </span>
                            <span className="align-items-start justify-content-end blog-pub-date"
                                style={{
                                    flex: 2,
                                    textAlign: "right",
                                    letterSpacing: "-0.5px"
                                }}>
                                {item?.pubDate && moment(item?.pubDate).format('LL')}
                            </span>
                        </p>
                        <h4 className="card-title blog-title" style={{
                            cursor: "pointer"
                        }} onClick={() => window.open(item?.link, "__blank")}>
                            {item?.title?.replace(/(&amp;)/ig, " & ")}
                        </h4>
                        <p className="card-text blog-content">
                            {item?.description.replace(/(<([^>]+)>)/ig, "").replace(/\"/ig, "").replace(/(&amp;)/ig, " & ").split(/\n/).slice(2).join("\n")}
                        </p>
                        <div style={{ height: "3px" }}></div>
                    </div>
                </div>
            </div>
        )
    }
    const renderOnePostMedium = (item: typeof props.blogs.posts[0]) => {
        return (
            <div className="col-lg-4 justify-content-center d-flex align-items-center" >
                <div className="card blog-card" style={{ margin: "20px 10px" }}>
                    <img onClick={() => window.open("https://cangokceaslan.medium.com/", "__blank")}
                        src={Medium}
                        className="card-img-top"
                        width="100%"
                        height="215px"
                        style={{
                            objectFit: "cover"
                        }}
                        alt={"Medium hesabıma erişerek yazılarıma ulaşabilirsin"} />
                    <div className="card-body blog-body">
                        <p className="blog-information d-flex align-items-center justify-content-center">
                            <span style={{ flex: 3 }}>Yazar:
                                <a className="blog-author"
                                    style={{
                                        color: globalStyles.primaryColor,
                                        fontFamily: 'inherit',
                                        fontWeight: 'bold',
                                        marginLeft: "10px"
                                    }}
                                    target="__blank"
                                    href={"https://cangokceaslan.medium.com/"}>
                                    {"Can Gökçeaslan"}
                                </a>
                            </span>
                            <span className="align-items-start justify-content-end blog-pub-date"
                                style={{
                                    flex: 2,
                                    textAlign: "right",
                                    letterSpacing: "-0.5px"
                                }}>
                                {moment(new Date().toString()).format('LL')}
                            </span>
                        </p>
                        <h4 className="card-title blog-title" style={{
                            cursor: "pointer"
                        }} onClick={() => window.open("https://cangokceaslan.medium.com", "__blank")}>
                            Medium hesabıma erişerek son postlarımdan haberdar olabilirsin.
                        </h4>
                        <p className="card-text blog-content">
                            Medium hesabımı ziyaret ederek özellikle yazılım ve teknoloji üzerine paylaşmış olduğum yazılara ulaşabilirsin. Hemen karta dokunarak yazmış olduğum son yazılardan haberdar olabilirsin.
                        </p>
                        <div style={{ height: "3px" }}></div>
                    </div>
                </div>
            </div>
        )
    }
    return (
        <section className="blog" id="blog">
            <h2 className="blog-header" style={{
                ...props?.hideTitle && {
                    marginTop: -80
                }
            }}>
                {!props?.hideTitle && (
                    <React.Fragment>
                        <img src="assets/svg/medium-sqr.svg" width="40px" height="37px" style={{ marginRight: "12px" }} />
                        <img src="assets/svg/medium-long.svg" width="150px" height="37px" style={{ marginRight: "12px" }} />
                    </React.Fragment>
                )}
            </h2>
            <div className="container-fluid top-container" style={{
                ...props?.hideTitle && (
                    sWidth < 992 && {
                        marginTop: "-40px"
                    }
                )
            }}>
                <div className="blog-container">
                    <div className="container">
                        <div className="row justify-content-center d-flex align-items-center">
                            {(props.posts || []).map((item, index) => renderOnePost(item))}
                            {renderOnePostMedium({})}
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
}

export default connect((state: { blogs: { posts: object, author: object, fetching: boolean } }) => ({
    posts: state?.blogs?.posts,
    author: state?.blogs?.author,
    fetching: state?.blogs?.fetching
}), {
    getPosts,
    toggleSendMessageModal
})(Blogs);